<template>
    <div id="app">
      <header>
        <h4>添加上级项目</h4>
      </header>
      <el-divider></el-divider>
      <!-- 下方盒子新增详情 -->
      <div class="box">
      <el-card shadow="hover">
      <div class="box-top">
        <el-form ref="form" label-width="80px">
        <ul>
          <li>
             <el-form-item label="项目编号">
              <el-input v-model="input" size="small" placeholder="请输入项目编号" clearable></el-input>
            </el-form-item>
          </li>
          <li>
            <el-form-item label="项目名称">
              <el-input v-model="input1" size="small" placeholder="请输入项目名称" clearable></el-input>
            </el-form-item>
          </li>
          <li>
            <el-form-item label="计划时间">
            <el-date-picker
            v-model="value"
            @change = 'change'
             size="small"
            type="daterange"
            align="right"
            unlink-panels
            value-format='yyyy-MM-dd'
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
          </li>
        </ul>
        </el-form>
      </div>
      <el-divider></el-divider>
      <!-- 中部内容 -->
      <div class="box-con">
        <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="name"
        label="文档名称"
        width="300px">
        <template slot-scope="scope">
          <el-input v-model="scope.row.name" size="small" placeholder="请输入文档名称" clearable></el-input>
        </template>
      </el-table-column>
      <el-table-column
        prop="brief"
        label="文档简介">
        <template slot-scope="scope">
          <el-input type="textarea" autosize v-model="scope.row.brief" size="small" placeholder="请输入文档简介" clearable></el-input>
        </template>
      </el-table-column>
      <el-table-column
        prop="upload"
        label="上传文件"
        width="200px">
        <template>

         <el-upload
         class="upload-demo"
         action="http://192.168.43.133:8080/projectFinal/insertProjectFinal"
         :on-change="handleChange">
        <!-- <el-button size="small" type="success">点击上传</el-button> -->
        <i style="font-size:30px;" class="el-icon-upload"></i>
        </el-upload>

        </template>
      </el-table-column>
      <el-table-column
        label="功能"
        width="150px">
        <template slot-scope="scope">
          <!-- <el-button type="primary" size="small" @click="add">{{scope.row.add}}</el-button> -->
          <i class="el-icon-plus" style="font-size:20px;" @click="add"></i>
          <i class="el-icon-minus" style="font-size:20px;" @click="del(scope.$index)"></i>
          <!-- <el-button type="danger" size="small" @click="del(scope.$index)">{{scope.row.del}}</el-button> -->
        </template>
      </el-table-column>
    </el-table>
      </div>
      <!-- 底部按钮 -->
      <div class="box-bot">
        <el-button type="warning" size="small" @click="preserve('addProject')">保存</el-button>
        <el-button type="info" size="small" @click="cancel('addProject')">取消</el-button>
      </div>
    </el-card>
    </div>
    </div>
</template>
<script>
export default {
    name:'add',
    data() {
      return {
        input: '',//项目编号
        input1:'',//项目名称
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [end, start]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() + 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [end, start]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() + 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [end, start]);
            }
          }]
        },
        value: '',//计划时间
        beginTime:'',//开始日期
        endTime:'',//结束日期
         fileList: [],
        tableData: [{
            name: '',
            brief: '',
            upload: '',
          },{
            name: '',
            brief: '',
            upload: '',
          },{
            name: '',
            brief: '',
            upload: '',
          },{
            name: '',
            brief: '',
            upload: '',
          },{
            name: '',
            brief: '',
            upload: '',
          }],
      }
    },
    methods:{
      handleChange(file,fileList){
        console.log(file);
        console.log(fileList);
      },
      add(){//新增
        this.tableData.push({
            name: '',
            brief: '',
            upload: '',
          })
      },
      del(id){//删除
      const h = this.$createElement;
        if(this.tableData.length==1){
          this.$notify({
          title: '错误的操作',
          message: h('i', { style: 'color: teal'}, '最后一条数据不可删除！')
        });
        }else{
          this.tableData.splice(id,1);
        }
      },
      change(data){//时间选择时改变
        this.beginTime = data[0];
        this.endTime = data[1];
      },
      preserve(zhi){//保存数据
        var newData = [];
        for(let i = 0;i<this.tableData.length;i++){
          if(this.tableData[i].name!=''&&this.tableData[i].brief!=''){
          newData.push(
              {
              documentName:this.tableData[i].name,//文档名称
              documentOverview:this.tableData[i].brief//文档简介
              })
          }
        }
        var jsonData = JSON.stringify({
          projectNum:this.input,//项目编号
          projectName:this.input1,//项目名称
          beginTime:this.beginTime,//开始日期
          endTime:this.endTime,//结束日期
          documentList:newData
        })
        console.log(jsonData);
        this.$axios.post('/projectFinal/insertProjectFinal',jsonData).then(res=>{
          console.log(res);
          if(res.data.code==200){
            this.$message({
            message: res.data.msg,
            type: 'success'
          });
          this.$router.push({
          name:zhi
          });
          }
          if(res.data.code==204){
            this.$message.error(res.data.msg);
          }

        })
      },
      cancel(zhi){//取消
        this.$router.push({
          name:zhi
        })
      }
    }
}
</script>
<style scoped>
  header h4{
    margin: 15px 0;
    margin-left: 15px;
  }
  /* 横线 */
  #app >>> .el-divider--horizontal{
    margin: 0;
  }
  /* 下方盒子新增详情 */
  .box{
    margin: 15px;
  }
  /* 盒子上方css */
  .box .box-top ul{
    margin: 0;
    padding: 0;
    margin-bottom: 15px;
    list-style: none;
    display: flex;
    justify-content: space-between;
  }
  .box .box-top ul >>> .el-form-item{
    margin: 0;
  }
  /* 中部内容 */
  .box-con >>> .el-table .cell{
    text-align: center;
    cursor: pointer;
    display: flex;
    justify-content: space-around;
  }
  .box-con >>> .el-upload__input{
    display: none;
  }
  .box-con >>> [class*=" el-icon-"], [class^=el-icon-]{
    flex: 1;
  }
  /* 底部按钮 */
  .box-bot{
    display: flex;
    width: 400px;
    margin: 0 auto;
    margin-top: 15px;
    justify-content: space-evenly
  }
  .box-bot button{
    width: 100px;
  }
</style>



